      --padding-x-pleine-largeur: calc(50vw - 1920px/2); } }

.site-main .fil-ariane, .site-main .entry-title {
  padding-left: var(--padding-x);
  padding-right: var(--padding-x); }

body.pleine-largeur .entry-content > p, body.pleine-largeur .entry-content > ul, body.pleine-largeur .entry-content > ol, body.pleine-largeur .entry-content > .wp-block-buttons {
