/* Hauteur mini main*/
.site {
  overflow-x: hidden;
  position: relative; }

.site-main {
  min-height: 100vh; }

/* Grid */
.flex {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center; }

.show-for-md,
.show-for-lg {
  display: none; }

