    display: none; } }

/* Overlay */
.site-main {
  position: relative; }

.overlay {
  background-color: #2D2F38;
  display: none;
  opacity: 0.8;
  transition: opacity var(--transition); }
  .overlay.toggled {
    display: block;
    height: 100%;
    width: 100%;
    z-index: 5;
    position: absolute;
    top: 0;
    left: 0; }

